export default {
  data() {
    return {
		info:[]
	}
	},
	onLoad:function() {
		// 地址列表
		var _this = this
		uni.request({
			url:'https://star.qhynice.top/api/address/getaddress',
			method:'GET',
			data:{
				user_id: 73
			},
			success(res) {
				console.log(res)
				_this.info = res.data.res
			}
		})
	},
	methods:{
		// 删除地址
		reset:function(id){
			var that = this
			that.id = id
			console.log(id)
			uni.request({
				url:'https://star.qhynice.top/api/address/del_adds',
				method:'POST',
				data:{
					user_id: 73,
					id: id
				},
				success: (res) => {
					let pages = getCurrentPages();	//获取加载的页面
					let page = pages[pages.length - 1];	//获取当前页面的对象
					page.onLoad()
				}
			})
		},
		add:function(){
			uni.navigateTo({
				url:'../tack/tack'
			})
		}
	}
}
